import React from "react";
import Footer from "../components/Footer";
import PagesNavBar from "../components/PagesNavBar";
import { Link } from "react-router-dom";
import pdf from "../pdf/PhysicsAndMeasurement.pdf";
import MotionInOneDimension from "../pdf/MotionInOneDimension.pdf";
import Vectors from "../pdf/Vectors.pdf";
import MotionInTwoDimensions from "../pdf/MotionInTwoDimensions.pdf";
import LawOfMotion from "../pdf/LawsOfMotion.pdf";
import ApplicationOfNewtonLaws from "../pdf/Application'sOfNewtonLaws.pdf";
import WorkAndKineticEnergy from "../pdf/WorkAndKineticEnergy.pdf";
import PotentialEnergyAndConservationOfEnergy from "../pdf/PotentialEnergyAndConservationOfEnergy.pdf";
import LinearMomentumAndCollisions from "../pdf/LinearMomentumAndCollisions.pdf";
import RotationOfARigidObject from "../pdf/RotationOfARigidObject.pdf";
import ScrollIntoView from "../components/ScrollIntoView";

class Statistics extends React.Component {
  openNav = () => {
    document.getElementById("mySidenav").style.width = "250px";
  };
  closeNav = () => {
    document.getElementById("mySidenav").style.width = "0";
  };

  render() {
    return (
      <ScrollIntoView>
        <div>
          <section>
            <div id="mySidenav" class="sidenav">
              <a
                href
                class="closebtn"
                style={{ cursor: "pointer" }}
                onClick={this.closeNav}
              >
                &times;
              </a>
              <Link to="/physics" style={{ fontWweight: "bold" }}>
                PART 1 MECHANICS
              </Link>
              <a href={pdf} target="_blank" rel="noopener noreferrer">
                Physics and Measurement
              </a>
              <a
                href={MotionInOneDimension}
                target="_blank"
                rel="noopener noreferrer"
              >
                Motion in One Dimension
              </a>
              <a href={Vectors} target="_blank" rel="noopener noreferrer">
                Vectors
              </a>
              <a
                href={MotionInTwoDimensions}
                target="_blank"
                rel="noopener noreferrer"
              >
                Motion in Two Dimensions
              </a>
              <a href={LawOfMotion} target="_blank" rel="noopener noreferrer">
                The Law of Motion
              </a>
              <a
                href={ApplicationOfNewtonLaws}
                target="_blank"
                rel="noopener noreferrer"
              >
                Circular Motion and Other Application of Newton's Laws
              </a>
              <a
                href={WorkAndKineticEnergy}
                target="_blank"
                rel="noopener noreferrer"
              >
                Work and Kinetic Energy
              </a>
              <a
                href={PotentialEnergyAndConservationOfEnergy}
                target="_blank"
                rel="noopener noreferrer"
              >
                Potential Energy and Conservation of Energy
              </a>
              <a
                href={LinearMomentumAndCollisions}
                target="_blank"
                rel="noopener noreferrer"
              >
                Linear Momentum and Collisions
              </a>
              <a
                href={RotationOfARigidObject}
                target="_blank"
                rel="noopener noreferrer"
              >
                Rotation of a Rigid Object About a Fixed Axis
              </a>

              <Link to="/">Angular Momentum</Link>
              <Link to="/">Static Equilibrium and Elasticity</Link>
              <Link to="/">Universal Gravitation</Link>
              <Link to="/">Fluid Mechanics</Link>
              <Link to="/" style={{ fontWweight: "bold" }}>
                PART 2 OSCILLATIONS AND MECHANICAL WAVES
              </Link>
              <Link to="/">Oscillatory Motion</Link>
              <Link to="/">Wave Motion</Link>
              <Link to="/">Superposition and Standing Waves</Link>
              <Link to="/">Sound Waves</Link>
              <Link to="/physics" style={{ fontWweight: "bold" }}>
                PART 3 THERMODYNAMICS
              </Link>
              <Link to="/">Temperature</Link>
              <Link to="/">The First Law of Thermodynamics</Link>
              <Link to="/">The Kinetic Theory of Gases</Link>
              <Link to="/">
                Heat Engines,Entropy and The Second Law of THERMODYNAMICS
              </Link>
              <Link to="/physics" style={{ fontWweight: "bold" }}>
                PART 4 ELECTRICITY AND MAGNETISM
              </Link>
              <Link to="/">Electric Fields</Link>
              <Link to="/">Gauss's Law</Link>
              <Link to="/">Electric Potential</Link>
              <Link to="/">Capacitance and Dielectrics</Link>
              <Link to="/">Current and Resistance</Link>
              <Link to="/">Direct Current Circuits</Link>
              <Link to="/">Magnetic Fields</Link>
              <Link to="/">Sources of the Magnetic Field</Link>
              <Link to="/">Faraday's Law</Link>
              <Link to="/">Alternating Current Circuits</Link>
              <Link to="/">Electromagnetic Waves</Link>
              <Link to="/" style={{ fontWweight: "bold" }}>
                PART 5 LIGHT AND OPTICS
              </Link>
              <Link to="/physics">
                The Nature of Light and the Laws of Geometric Optics
              </Link>
              <Link to="/">Image Formation</Link>
              <Link to="/">Interference of Light Waves</Link>
              <Link to="/">Diffraction Patterns and Polarization</Link>
              <Link to="/physics" style={{ fontWweight: "bold" }}>
                PART 6 MODERN PHYSICS
              </Link>
              <Link to="/">Relativity</Link>
            </div>
          </section>
          {/* top navbar */}
          <nav class="navbar navbar-expand-lg navbar-dark  navBarTop ">
            <div class="container-fluid opacity-75">
              <span
                style={{ fontSize: "20px", cursor: "pointer", color: "white" }}
                onClick={this.openNav}
              >
                &#9776; Topics
              </span>
            </div>
          </nav>

          <PagesNavBar />

          <section>
            {/* short words */}
            <section class="container-fluid">
              <hr />
              <div>
                <h5 style={{ color: "red", fontWeight: "15px" }}>
                  What is Statistics
                </h5>
                <p>
                  The study or analysis of a set of data to ascertain or
                  interpret various things is all that statistics is. Other
                  definitions of statistics include classified statistics that
                  reflect the condition or state of individuals. All of the
                  mathematical puzzles posed in statistics can be solved in
                  three steps. Data collection comes first, followed by data
                  summarization and statistical analysis as the final step.
                </p>
              </div>
            </section>

            <section class="container-fluid">
              <div>
                <h5 style={{ color: "red", fontWeight: "15px" }}>Study Tips for the Student of Basic Statistics </h5>
                <ol>
                  <li>
                    Instead of mass practice, use distributive practice. To
                    study statistics, set aside one to two hours per day at the
                    same time for six days of the week (leave the seventh day
                    off). Avoid cramming four or five hours of study into one or
                    two sessions per week. This is a fundamental idea.
                  </li>
                  <li>
                    At least once per week, students should study in triads or
                    quads. A deeper level of understanding is actually cemented
                    through verbal exchange and interpretation of concepts and
                    skills with other students.
                  </li>
                  <li>
                    Try not to try to remember formulas (A good instructor will
                    never ask you to do this). Concepts, concepts, concepts:
                    study. Remember that you can always look up the formula in a
                    textbook later in life when you need to use a statistical
                    technique.
                  </li>
                  <li>
                    Work as many different exercises and problems as you can.
                    Ideally, a workbook is included with your textbook. Simply
                    reading about statistics won't teach you anything about it.
                    Pushing the pencil and repeatedly honing your techniques are
                    required.
                  </li>
                  <li>
                    In statistics, look for recurring themes. There are probably
                    only a small number of crucial skills that come up
                    repeatedly. If necessary, request that your instructor
                    emphasize these.
                  </li>
                  <li>
                    Become a Gestalt therapist! Recognize that statistics as a
                    whole is greater than the sum of its parts. It is very
                    simple to become preoccupied with minor details and lose
                    sight of the bigger picture.
                  </li>
                  <li>
                    Act if you suffer from math or statistics anxiety, which
                    affects about 70% of people in general. Most universities
                    offer top-notch counseling programs to lessen this
                    disability because they recognize how crippling this issue
                    is. Get assistance for yourself. This could end up being the
                    wisest choice you make during your undergraduate career.
                  </li>
                </ol>
              </div>
            </section>
          </section>
          <br />
          <br />
          <Footer />
        </div>
      </ScrollIntoView>
    );
  }
}

export default Statistics;
